// WishlistPage.tsx

"use client";
import { useRouter } from "next/navigation";
import { Trash2 } from "lucide-react";
import { Button } from "@/components/ui/button";
import Image from "next/image";

import { useWishlist } from "@/context/WishListContext";

export default function WishlistPage() {
    const { wishlistItems, removeFromWishlist } = useWishlist();
    const router = useRouter();

    return (
        <main className="min-h-screen flex flex-col items-center p-4 sm:p-6 bg-gray-500/30 dark:bg-gray-900/90 text-gray-800 dark:text-white relative z-0">
            {/* Fondo de cielo */}
            <div className="absolute inset-0 -z-10 overflow-hidden">
                <Image
                    src="/images/sky.jpg"
                    alt="Cielo de Springfield"
                    fill
                    className="object-cover object-top opacity-50"
                />
            </div>

            <div className="w-full max-w-4xl mx-auto z-1">
                <h1 className="font-simpson text-5xl text-center my-8 text-blue-600 dark:text-gray-300 drop-shadow-sm">
                    Mi Lista de Deseos
                </h1>

                {wishlistItems.length === 0 ? (
                    /* --- VISTA CUANDO LA LISTA ESTÁ VACÍA --- */
                    <div className="relative bg-white/80 dark:bg-accent-foreground/90 backdrop-blur-md rounded-2xl shadow-2xl p-10 flex flex-col items-center gap-6 text-center">
                        <h2 className="font-simpson text-3xl text-red-600">
                            ¡Ay, caramba!
                        </h2>
                        <p className="text-gray-700 dark:text-gray-800 text-lg">
                            Tu lista de deseos está más vacía que la tienda de
                            Herb Powell.
                            <br />
                            ¡Vuelve a la tienda y añade tus productos favoritos!
                        </p>
                        <Image
                            src="/images/kwik-wall.png"
                            alt="Apu triste"
                            width={300}
                            height={300}
                            className="select-none"
                        />
                        <Button
                            onClick={() => router.push("/productos")}
                            className="mt-4 px-6 py-2 rounded-xl bg-green-500 hover:bg-green-600 text-white font-semibold shadow-lg cursor-pointer">
                            Volver a la tienda
                        </Button>
                    </div>
                ) : (
                    /* --- VISTA DE LISTA DE PRODUCTOS --- */
                    <div className="flex flex-col gap-4">
                        {wishlistItems.map((item) => (
                            <div
                                key={item.id}
                                className="flex items-center gap-4 p-4 bg-white/90 dark:bg-accent-foreground/90 rounded-xl shadow-md transition-all duration-300 hover:shadow-lg">
                                {/* Contenedor de Imagen */}
                                <div className="flex-shrink-0 w-24 h-24 bg-gray-100 dark:bg-gray-50 rounded-lg flex items-center justify-center overflow-hidden">
                                    <Image
                                        src={
                                            item.image.startsWith("/")
                                                ? item.image
                                                : `/images/${item.image}`
                                        }
                                        alt={item.name}
                                        width={100}
                                        height={100}
                                        className="w-full h-full object-contain"
                                    />
                                </div>

                                {/* Detalles del Producto */}
                                <div className="flex-grow">
                                    <h3 className="font-simpson text-xl sm:text-2xl text-blue-700">
                                        {item.name}
                                    </h3>
                                    <p className="font-bold text-lg text-green-600">
                                        ${item.price.toFixed(2)}
                                    </p>
                                </div>

                                {/* Botón de Eliminar */}
                                <div className="flex-shrink-0">
                                    <Button
                                        variant="ghost"
                                        size="icon"
                                        className="text-red-500 hover:bg-red-100 dark:hover:bg-red-200 hover:text-red-600 rounded-full cursor-pointer"
                                        onClick={() =>
                                            removeFromWishlist(item.id)
                                        }>
                                        <Trash2 className="w-5 h-5" />
                                        <span className="sr-only">Quitar</span>
                                    </Button>
                                </div>
                            </div>
                        ))}
                    </div>
                )}
            </div>
        </main>
    );
}
